
.body-1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 10px;
   
}
.perfil-ft{
    width: 15vh;
    height: 15vh;
}

.body-1 p{
    padding: 10px;
}
#perfil{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#perfil h1{
    color: rgb(214, 214, 214);
}
#perfil h3{
    color: #53799e;
}
#perfil h6{
    color: rgb(158, 158, 158);
}
#sobremim{
    background-color: white;
    margin-top: 10vh;
    width: 100%;
}
#sobremim h2 {
    color: black;
    
    border-bottom: 1px solid black;
    text-align: center;
}
#contato{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    width: 100%;

    margin-top: 5px;
  
}
#contato h3{
    border-bottom: 1px solid black;
   
}
